/* jshint esversion: 6 */
import React from 'react';
import { useEffect, useState } from '@wordpress/element';
import BuilderColumns from './BuilderColumns';
const BuilderColumnsComponent = ({ control }) => {
    const { setting, params } = control;
    const { label, choices, columnsControl } = params;
    const [value, setValue] = useState(setting.get());
    const [columnsNo, setColumnsNo] = useState(1);
    const onChange = (nextValue) => {
        setValue(nextValue);
        control.setting.set(nextValue);
    };
    useEffect(() => {
        window.wp.customize.bind('ready', () => {
            const colNumber = window.wp.customize
                .control(columnsControl)
                .setting.get();
            setColumnsNo(parseInt(colNumber));
        });
        window.wp.customize.control(columnsControl, (customizeControl) => {
            customizeControl.setting.bind((nextColNumber) => {
                const colNumberInt = parseInt(nextColNumber);
                const currentValue = setting.get();
                if (!Object.keys(choices[colNumberInt]).includes(currentValue)) {
                    const firstValue = Object.keys(choices[colNumberInt])[0];
                    onChange(firstValue);
                }
                setColumnsNo(colNumberInt);
            });
        });
    });
    return (React.createElement(BuilderColumns, { value: value, columns: columnsNo, onChange: onChange, label: label, choices: choices }));
};
export default BuilderColumnsComponent;
