/* jshint esversion: 6 */
import { __ } from '@wordpress/i18n';
import { Toggle } from '@solace-wp/components';
import PropTypes from 'prop-types';
import { useState, useEffect } from '@wordpress/element';

const ToggleComponentFlex = ({ control }) => {
	const { label, description } = control.params;
	const [value, setValue] = useState(control.setting.get());
	const [isActive, setIsActive] = useState(false);

	useEffect(() => {
		document.addEventListener('solace-changed-customizer-value', (e) => {
			if (!e.detail) return false;
			if (e.detail.id !== control.id) return false;
			if (e.detail.value === value) return false;
			toggleValue(e.detail.value);
		});
	}, []);

	const toggleValue = (val) => {
		setValue(val);
		control.setting.set(val);
		setIsActive(!isActive);
	};

	return (
		<Toggle
			description={description}
			label={isActive ? __('Hide ' + label, 'solace') : __('Show ' + label, 'solace')}
			checked={value}
			onChange={toggleValue}
		/>
	);
};

ToggleComponentFlex.propTypes = {
	control: PropTypes.object.isRequired,
};

export default ToggleComponentFlex;
