/*eslint camelcase: ["error", {allow: ["is_for","large_buttons","user_icon_style1","user_icon_style2","user_icon_style3","user_icon_style4","user_icon_style5",,"user_icon_style6","user_avatar"]}]*/
import PropTypes from 'prop-types';
import { SVG, RadioIcons } from '@solace-wp/components';

import { __ } from '@wordpress/i18n';
import { useState, useEffect } from '@wordpress/element';
import { code } from '@wordpress/icons';

const RadioButtonsComponent = ({ control }) => {
	const [value, setValue] = useState(control.setting.get());
	const { custom } = control.settings;

	useEffect(() => {
		document.addEventListener('solace-changed-customizer-value', (e) => {
			if (!e.detail) return false;
			if (e.detail.id !== control.id) return false;
			updateValue(e.detail.value);
		});
	}, []);

	const updateValue = (newVal) => {
		setValue(newVal);
		control.setting.set(newVal);
	};

	const getChoices = () => {
		const { is_for, choices } = control.params;

		if (!is_for) {
			return choices;
		}

		switch (is_for) {
			case 'button_base':
				return {
					button1: {
						tooltip: __('Button 1', 'solace'),
						icon: SVG.button1,
					},
					button2: {
						tooltip: __('Button 2', 'solace'),
						icon: SVG.button2,
					},
				};
			case 'logo':
				return {
					default: {
						tooltip: __('Logo Only', 'solace'),
						icon: SVG.logoOnly,
					},
					logoTitle: {
						tooltip: __('Logo - Title & Tagline', 'solace'),
						icon: SVG.logoTitle,
					},
					// titleLogo: {
					// 	tooltip: __('Title & Tagline - Logo', 'solace'),
					// 	icon: SVG.titleLogo,
					// },
					// logoTopTitle: {
					// 	tooltip: __('Logo on Top', 'solace'),
					// 	icon: SVG.logoTopTitle,
					// },
				};
			case 'menu':
				return {
					'style-plain': {
						tooltip: __('Plain', 'solace'),
						icon: SVG.menuPlain,
					},
					'style-full-height': {
						tooltip: __('Background', 'solace'),
						icon: SVG.menuFilled,
					},
					'style-border-bottom': {
						tooltip: __('Bottom Border', 'solace'),
						icon: SVG.menuUnderline,
					},
					'style-border-top': {
						tooltip: __('Top Border', 'solace'),
						icon: SVG.menuOverline,
					},
				};
			case 'account_component':
				return {
					user_icon_style1: {
						tooltip: __('Icon Style 2', 'solace'),
						icon: SVG.myAccountUserSolid,
					},
					user_icon_style2: {
						tooltip: __('Icon Style 1', 'solace'),
						icon: SVG.myAccountUserRegular,
					},
					user_icon_style3: {
						tooltip: __('Icon Style 3', 'solace'),
						icon: SVG.myAccountUserTagSolid,
					},
					user_icon_style4: {
						tooltip: __('Icon Style 4', 'solace'),
						icon: SVG.myAccountUserAltSolid,
					},
					user_icon_style5: {
						tooltip: __('Icon Style 5', 'solace'),
						icon: SVG.myAccountUsersSolid,
					},
					user_icon_style6: {
						tooltip: __('Icon Style 6', 'solace'),
						icon: SVG.myAccountUsersFirendsSolid,
					},
					user_avatar: {
						tooltip: __('WordPress Avatar', 'solace'),
						icon: SVG.myAccountUserAvatar,
					},
					none: {
						tooltip: __('No Icon', 'solace'),
						icon: SVG.myAccountNone,
					},
				};
			case 'cart_component':
				return {
					'cart-icon-style1': {
						tooltip: __('Style 1', 'solace'),
						icon: SVG.cartIconStyle1,
					},
					'cart-icon-style2': {
						tooltip: __('Style 2', 'solace'),
						icon: SVG.cartIconStyle2,
					},
					'cart-icon-style3': {
						tooltip: __('Style 3', 'solace'),
						icon: SVG.cartIconStyle3,
					},
					'cart-icon-style4': {
						tooltip: __('Style 4', 'solace'),
						icon: SVG.cartIconStyle4,
					},
					'cart-icon-style5': {
						tooltip: __('Style 5', 'solace'),
						icon: SVG.cartIconStyle5,
					},
					'cart-icon-style6': {
						tooltip: __('Style 6', 'solace'),
						icon: SVG.cartIconStyle6,
					},
				};
			case 'palette_switch':
				return {
					contrast: {
						tooltip: __('Contrast', 'solace'),
						icon: SVG.paletteIconStyle1,
					},
					night: {
						tooltip: __('Night', 'solace'),
						icon: SVG.paletteIconStyle2,
					},
					toggle: {
						tooltip: __('Toggle', 'solace'),
						icon: SVG.paletteIconStyle3,
					},
					accessibility: {
						tooltip: __('Accessibility', 'solace'),
						icon: SVG.paletteIconStyle4,
					},
				};
			case 'account':
				return {
					account1: {
						tooltip: __('Account1', 'solace'),
						icon: SVG.accountIconStyle1,
					},
					account2: {
						tooltip: __('Account2', 'solace'),
						icon: SVG.accountIconStyle2,
					},
					account3: {
						tooltip: __('Account3', 'solace'),
						icon: SVG.accountIconStyle3,
					},
					account4: {
						tooltip: __('Account4', 'solace'),
						icon: SVG.accountIconStyle4,
					},
					account5: {
						tooltip: __('Account5', 'solace'),
						icon: SVG.accountIconStyle5,
					},
				};	
			case 'sol_search':
				return {
					search1: {
						tooltip: __('Search1', 'solace'),
						icon: SVG.searchIconStyle1,
					},
					search2: {
						tooltip: __('Search2', 'solace'),
						icon: SVG.searchIconStyle2,
					},
					search3: {
						tooltip: __('Search3', 'solace'),
						icon: SVG.searchIconStyle3,
					},
					search4: {
						tooltip: __('Search4', 'solace'),
						icon: SVG.searchIconStyle4,
					},
					search5: {
						tooltip: __('Search5', 'solace'),
						icon: SVG.searchIconStyle5,
					},
				};	
			case 'sol_cart':
				return {
					cart1: {
						tooltip: __('Cart1', 'solace'),
						icon: SVG.cartIconStyle1,
					},
					cart2: {
						tooltip: __('Cart2', 'solace'),
						icon: SVG.cartIconStyle2,
					},
					cart3: {
						tooltip: __('Cart3', 'solace'),
						icon: SVG.cartIconStyle3,
					},
					cart4: {
						tooltip: __('Cart4', 'solace'),
						icon: SVG.cartIconStyle4,
					},
					cart5: {
						tooltip: __('Cart5', 'solace'),
						icon: SVG.cartIconStyle5,
					},
				};
			case 'sol_menu':
				return {
					menu1: {
						tooltip: __('Menu1', 'solace'),
						icon: SVG.menuIconStyle1,
					},
					menu2: {
						tooltip: __('Menu2', 'solace'),
						icon: SVG.menuIconStyle2,
					},
					menu3: {
						tooltip: __('Menu3', 'solace'),
						icon: SVG.menuIconStyle3,
					},
					menu4: {
						tooltip: __('Menu4', 'solace'),
						icon: SVG.menuIconStyle4,
					},
					menu5: {
						tooltip: __('Menu5', 'solace'),
						icon: SVG.menuIconStyle5,
					},
				};
			case 'scroll_to_top':
				return {
					'stt-icon-style-1': {
						tooltip: __('Icon Style 1', 'solace'),
						icon: SVG.sttIcon1,
					},
					'stt-icon-style-2': {
						tooltip: __('Icon Style 2', 'solace'),
						icon: SVG.sttIcon2,
					},
					'stt-icon-style-3': {
						tooltip: __('Icon Style 3', 'solace'),
						icon: SVG.sttIcon3,
					},
					'stt-icon-style-4': {
						tooltip: __('Icon Style 4', 'solace'),
						icon: SVG.sttIcon4,
					},
					'stt-icon-style-5': {
						tooltip: __('Icon Style 5', 'solace'),
						icon: SVG.sttIcon5,
					},
					'stt-icon-style-6': {
						tooltip: __('Icon Style 6', 'solace'),
						icon: SVG.sttIcon6,
					},
				};
			case 'menu_caret':
				return {
					'mc-icon-style-1': {
						tooltip: __('Icon Style 1', 'solace'),
						icon: SVG.mcIcon1,
					},
					'mc-icon-style-2': {
						tooltip: __('Icon Style 2', 'solace'),
						icon: SVG.mcIcon2,
					},
					'mc-icon-style-3': {
						tooltip: __('Icon Style 3', 'solace'),
						icon: SVG.mcIcon3,
					},
					'mc-icon-style-4': {
						tooltip: __('Icon Style 4', 'solace'),
						icon: SVG.mcIcon4,
					},
					'mc-icon-style-5': {
						tooltip: __('Icon Style 5', 'solace'),
						icon: SVG.mcIcon5,
					},
					'mc-icon-style-6': {
						tooltip: __('Icon Style 6', 'solace'),
						icon: SVG.mcIcon6,
					},
				};
		}
	};
	const { label, large_buttons, showLabels } = control.params;

	const options = getChoices();
	if (custom) {
		options.custom = {
			tooltip: __('Custom SVG', 'solace'),
			icon: code,
		};
	}

	return (
		<div className="solace-white-background-control">
			{label && <span className="customize-control-title">{label}</span>}
			<RadioIcons
				largeButtons={large_buttons}
				showLabels={showLabels}
				value={value}
				customSetting={custom}
				options={options}
				onChange={updateValue}
			/>
		</div>
	);
};

RadioButtonsComponent.propTypes = {
	control: PropTypes.object.isRequired,
};

export default RadioButtonsComponent;
