import React from 'react';
import { __ } from '@wordpress/i18n';
import { useEffect, useState } from '@wordpress/element';
// @ts-ignore
import { InlineSelect } from '@solace-wp/components';
import { Button } from '@wordpress/components';
const SkinSwitcher = ({ control }) => {
    const [value, setValue] = useState(control.setting.get());
    const [changed, setChanged] = useState(false);
    const [switching, setSwitching] = useState(false);
    const [customizerSaved, setCustomizerSaved] = useState(true);
    useEffect(() => {
        window.wp.customize.bind('ready', () => {
            setCustomizerSaved(window.wp.customize.state('saved').get());
            window.wp.customize.state('saved').bind((status) => {
                setCustomizerSaved(status);
            });
        });
    }, []);
    const options = [
        {
            value: 'old',
            label: __('Legacy Skin', 'solace'),
        },
        {
            value: 'new',
            label: __('Solace 3.0', 'solace'),
        },
    ];
    const handleChange = (newVal) => {
        setValue(newVal);
        setChanged(true);
    };
    const handleSwitch = () => {
        setSwitching(true);
        setVal(control, value).then(() => {
            window.wp.customize.previewer.save().then(() => {
                window.wp.customize.notifications.add(new window.wp.customize.OverlayNotification('solace_switching_skin', {
                    message: __('Switching skin', 'solace') + '...',
                    type: 'success',
                    loading: true,
                }));
                window.location.reload();
            });
        });
    };
    return (React.createElement("div", { className: "solace-white-background-control" },
        React.createElement(InlineSelect, { disabled: !customizerSaved, label: __('Style', 'solace'), onChange: handleChange, options: options, value: value }),
        React.createElement(Button, { isPrimary: true, onClick: handleSwitch, disabled: !changed ||
                !customizerSaved ||
                control.setting.get() === value }, __('Switch Skin', 'solace')),
        !customizerSaved && !switching && (React.createElement("p", { className: "description" }, __('Please save the current customizer settings before switching skins.')))));
};
const setVal = async (control, newVal) => {
    await control.setting.set(newVal);
};
export default SkinSwitcher;
