/* jshint esversion: 6 */
import PropTypes from 'prop-types';
import { ColorControl } from '@solace-wp/components';

import { useState, useEffect } from '@wordpress/element';

const ColorComponent = ({ control, children }) => {
	const [value, setValue] = useState(control.setting.get());

	const updateValues = (newVal) => {
		setValue(newVal);
		control.setting.set(newVal);
	};

	useEffect(() => {
		document.addEventListener('solace-changed-customizer-value', (e) => {
			if (!e.detail) return false;
			if (e.detail.id !== control.id) return false;
			updateValues(e.detail.value);
		});
	}, []);

	return (
		<div className="solace-white-background-control solace-color-control">
			<ColorControl
				label={control.params.label}
				idControl={control.params.settings.default}
				selectedColor={value}
				defaultValue={control.params.default}
				alphaDisabled={control.params.disableAlpha}
				onChange={updateValues}
				allowGradient={control.params.allowGradient}
			>
				{children}
			</ColorControl>
		</div>
	);
};

ColorComponent.propTypes = {
	control: PropTypes.object.isRequired,
};

export default ColorComponent;
