import React from 'react';
import FontPairSelector from './FontPairSelector';
const FontPairComponent = ({ control }) => {
    const { pairs, description } = control.params.input_attrs;
    const handleClick = (preset) => {
        window.document.dispatchEvent(new window.CustomEvent('solace-changed-customizer-value', {
            detail: {
                value: preset.bodyFont,
                id: 'solace_body_font_family',
            },
        }));
        window.document.dispatchEvent(new window.CustomEvent('solace-changed-customizer-value', {
            detail: {
                value: preset.headingFont,
                id: 'solace_headings_font_family',
            },
        }));
    };
    return (React.createElement(FontPairSelector, { description: description, onSelect: handleClick, pairs: pairs }));
};
export default FontPairComponent;
