import { Button, Modal, Popover } from '@wordpress/components';
import { useState } from '@wordpress/element';
import { __, sprintf } from '@wordpress/i18n';
import { trash } from '@wordpress/icons';
import classnames from 'classnames';

const PaletteSelector = ({ values, save }) => {
	const { palettes, activePalette } = values;
	const [isOpenModal, setIsOpenModal] = useState(false);
	const [willDelete, setWillDelete] = useState('');
	const [visible, setVisible] = useState(false);

	const PREVIEW_COLORS = [
		'sol-color-base-font',
		'sol-color-heading',
		'sol-color-link-button-initial',
		'sol-color-link-button-hover',
		'sol-color-background',
		'sol-color-page-title-background',
	];

	const svgIconCheck = '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path d="M256 512A256 256 0 1 0 256 0a256 256 0 1 0 0 512zM369 209L241 337c-9.4 9.4-24.6 9.4-33.9 0l-64-64c-9.4-9.4-9.4-24.6 0-33.9s24.6-9.4 33.9 0l47 47L335 175c9.4-9.4 24.6-9.4 33.9 0s9.4 24.6 0 33.9z"/></svg>';

	const svgIconArrowBottom = '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512"><path d="M201.4 342.6c12.5 12.5 32.8 12.5 45.3 0l160-160c12.5-12.5 12.5-32.8 0-45.3s-32.8-12.5-45.3 0L224 274.7 86.6 137.4c-12.5-12.5-32.8-12.5-45.3 0s-12.5 32.8 0 45.3l160 160z"/></svg>';

	const [isActive, setIsActive] = useState(false);

	function toggleButton() {
	  setIsActive(!isActive);
	}

	const deletePalette = () => {
		const nextValues = { ...values };
		if (activePalette === willDelete) {
			nextValues.activePalette = 'base';
		}
		delete nextValues.palettes[willDelete];
		setIsOpenModal(false);
		setWillDelete('');
		save(nextValues);
	};

	const setActivePalette = (id) => {
		const nextValues = { ...values };
		nextValues.activePalette = id;

		save(nextValues);
	};

	// Reorder the palette keys so we always have first positions used by defaults.
	const orderedPaletteKeys = [
		...Object.keys(values.palettes).filter(
			(paletteSlug) => !palettes[paletteSlug].allowDeletion
		),
		...Object.keys(values.palettes).filter(
			(paletteSlug) => palettes[paletteSlug].allowDeletion
		),
	];

	return (
		<>
		<div
			className={isActive ? "palettes-wrap-top active" : "palettes-wrap-top"}
			onClick={() => {
				setVisible(true);
				// isActive ? setVisible(false) : setVisible(true);
			}}
		>
			{orderedPaletteKeys.map((id) => {
				const { colors, allowDeletion, name } = palettes[id];
				const colorActive = activePalette === id;
				const paletteClasses = classnames([
					'solace-global-color-palette-inner',
					{
						active: activePalette === id,
					},
				]);
				return (
					<>
						{colorActive && 
							<>
							<div className="box-color box-color-groups">
								{PREVIEW_COLORS.map((color, index, arr) => {
									return (
										<div
											className={`${arr[index]} color`}
											key={index}
											index={index}
											dataColor={arr[index]}
											style={{
												background: colors[color],
											}}
										/>
									);
								})}
							</div>
							<div className="box-icon" onClick={toggleButton}>
								<div className="icon" dangerouslySetInnerHTML={{ __html: svgIconArrowBottom }} />
							</div>
							</>
						}
					</>
				);
			})}
		</div>

		<div className="container-solace-palettes-wrap">
			{visible && (
				<Popover
					onFocusOutside={() => {
						setVisible(false);
					}}					
				>				
					<div className="solace-palettes-wrap">
						{orderedPaletteKeys.map((id) => {
							const { colors, allowDeletion, name } = palettes[id];
							const paletteClasses = classnames([
								'solace-global-color-palette-inner',
								{
									active: activePalette === id,
								},
							]);
							return (
								<div key={id} className="solace-global-color-palette">
									{allowDeletion && (
										<>
											<Button
												isLink
												icon={trash}
												iconSize={21}
												className="delete-palette"
												title={__('Remove Palette', 'solace')}
												onClick={() => {
													setWillDelete(id);
													setIsOpenModal(true);
												}}
											/>
											{isOpenModal && (
												<Modal
													isDismissible={false}
													className="solace-global-colors-confirm-delete-modal"
													title={sprintf(
														// translators: %s - name of palette that will be deleted.
														__(
															'Are you sure you want to delete the %s palette?',
															'solace'
														),
														palettes[willDelete].name
													)}
												>
													<p>
														{__(
															'If this is the currently active palette, the current palette will be switched to the Base one.',
															'solace'
														)}
													</p>
													<div className="actions">
														<Button
															isPrimary
															icon="trash"
															onClick={deletePalette}
														>
															{__('Delete', 'solace')}
														</Button>
														<Button
															isSecondary
															onClick={() => {
																setIsOpenModal(false);
																setWillDelete('');
															}}
														>
															{__('Cancel', 'solace')}
														</Button>
													</div>
												</Modal>
											)}
										</>
									)}
									<button
										className={paletteClasses}
										onClick={(e) => {
											e.preventDefault();
											setActivePalette(id);
										}}
										key={name.toLowerCase()}
									>
										<div className="box-title">
											<span className="title">{name}</span>
										</div>

										<div className="box-color">
											{PREVIEW_COLORS.map((color, index, arr) => {
												return (
													<div
														className={`${arr[index]} color`}
														key={index}
														index={index}
														dataColor={arr[index]}
														style={{
															background: colors[color],
														}}
													/>
												);
											})}
										</div>

										<div className="box-icon">
											<div className="icon" dangerouslySetInnerHTML={{ __html: svgIconCheck }} />
										</div>
									</button>
								</div>
							);
						})}
					</div>
				</Popover>
			)}
		</div>
		</>
	);
};

export default PaletteSelector;
